@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/ubuntu/regular.ttf") format("truetype"); }

@font-face {
  font-family: "Ubuntu";
  font-style: italic;
  font-weight: normal;
  src: url("../fonts/ubuntu/italic.ttf") format("truetype"); }

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/ubuntu/medium.ttf") format("truetype"); }

@font-face {
  font-family: "Ubuntu";
  font-style: italic;
  font-weight: 500;
  src: url("../fonts/ubuntu/medium-italic.ttf") format("truetype"); }

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: bold;
  src: url("../fonts/ubuntu/bold.ttf") format("truetype"); }

@font-face {
  font-family: "Ubuntu";
  font-style: italic;
  font-weight: bold;
  src: url("../fonts/ubuntu/bold-italic.ttf") format("truetype"); }

*, *:before, *:after {
  box-sizing: border-box; }

body, html {
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  line-height: normal;
  font-weight: 400;
  color: #ffffff;
  background-color: black;
  background-image: url("../img/bg.jpg");
  background-size: cover;
  background-position: center; }

a {
  text-decoration: none;
  transition: all .2s; }
  a:active {
    transform: scale(0.9); }

main {
  transition: all .4s;
  overflow: hidden; }
  main .containers {
    transition: all .4s;
    display: flex;
    position: relative;
    left: 0;
    will-change: left; }
  main .container {
    transition: all .4s;
    width: 100vw;
    flex-shrink: 0;
    min-height: 100vh;
    padding-top: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; }

nav a {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 2px;
  padding: 15px;
  display: inline-block; }
  nav a:hover {
    opacity: .7; }

header {
  display: flex;
  justify-content: flex-end;
  padding-right: 20%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2; }

h1 {
  font-family: 'Open Sans', sans-serif;
  color: #ffffff;
  font-size: 90px;
  text-align: center;
  max-width: 70%; }

.img-container {
  width: 160px;
  height: 160px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .img-container img {
    border-radius: 50%;
    max-width: 100%;
    max-height: 100%;
    width: auto !important;
    height: auto !important; }

.person {
  display: flex;
  color: #ffffff;
  width: 800px; }
  .person__left-side {
    margin-right: 20px; }

.slide {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .slide:first-child {
    height: calc(100vh - 46px); }

.edu, .work {
  width: 800px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start; }
  .edu__title, .work__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    border-bottom: 5px solid salmon; }
  .edu li, .work li {
    margin-bottom: 30px; }
    .edu li:last-child, .work li:last-child {
      margin-bottom: 0; }

.socials ul {
  display: flex;
  margin-top: 0;
  margin-bottom: 0; }
  .socials ul li {
    margin-right: 40px;
    list-style-type: none; }
    .socials ul li:last-child {
      margin-right: 0; }
    .socials ul li a {
      display: inline-block;
      font-size: 40px;
      color: #ffffff !important;
      transition: all .2s; }
      .socials ul li a:hover {
        opacity: .7; }
